<template>
  <div class="inline-block">
    <Button
      v-if="!$slots.trigger"
      :disabled="disabled"
      color="white"
      @click="handleShowValidation">
      <div
        :class="[
          'transition-all duration-300 ease-in-out',
          'text-grey-03 hover:text-grey-04 flex items-center gap-x-3'
        ]">
        <span class="material-icons">table_view</span>
        <span>Price Upload</span>
      </div>
    </Button>
    <slot
      v-if="$slots.trigger"
      name="trigger"
      v-bind="{
        click: handleShowValidation,
        disabled
      }">
    </slot>
  </div>
  <Modal
    v-model="modal"
    size="md"
    no-close>
    <template #header>
      <div>
        <span class="font-medium">
          Price Upload
        </span>
        <a v-if="!loading" href="#" class="modal__content-header-close top-0 right-0" @click.prevent="handleClose">âœ•</a>
      </div>
    </template>
    <template #default>
      <div v-loading="loading">
        <div v-if="!state?.inProgress" class="space-y-8">
          <div class="space-y-4">
            <div class="flex items-center gap-4">
              <div
                :class="[
                  'bg-teal-04 w-7 h-7',
                  'rounded-full flex',
                  'items-center justify-center',
                  'text-white'
                ]">
                1
              </div>
              <span class="font-medium text-base">Download Template</span>
            </div>
            <div class="pl-11">
              <PriceUploadDownloadTemplate
                :modal-cancel="modalCancelDownload"
                :disabled="!!state.latestUploadedFile?.length"
                @download="handleDownload"
                @continue-download="handleCloseWarning"
                @confirm-close="handleConfirmClose"
              />
            </div>
          </div>
          <div class="space-y-4">
            <div class="flex items-center gap-4">
              <div
                :class="[
                  'bg-teal-04 w-7 h-7',
                  'rounded-full flex',
                  'items-center justify-center',
                  'text-white'
                ]">
                2
              </div>
              <span class="font-medium text-base">Import Data</span>
            </div>
            <div class="pl-11">
              <PriceUploadImportData
                :files="state.latestUploadedFile"
                :loading="loading || loadingDownload || storeState?.downloadProgress > 0"
                @completed="handleUploadCompleted"
                @error="handleUploadError"
                @uploading="handleUploading"
                @remove="handleRemovedFile"
              />
              <!-- <div v-if="loadingDownload || storeState?.downloadProgress > 0" class="absolute top-0 left-0 right-0 bottom-0 bg-white opacity-50 cursor-not-allowed" /> -->
            </div>
          </div>
        </div>
        <div v-else>
          <PriceUploadInProgress />
        </div>
      </div>
    </template>
    <template #footer>
      <div>
        <Button
          :disabled="loading || !state?.latestUploadedFile?.length || state?.inProgress"
          color="teal"
          @click="handleSubmit">
          <div class="flex items-center justify-center gap-x-2">
            <span>Update Pricing</span>
          </div>
        </Button>
      </div>
    </template>
  </Modal>
</template>

<script>
import { defineComponent, ref } from 'vue'
import { usePriceUpload } from 'composable/pricing'
import { usePaywall } from 'composable/paywall'

import Modal from 'bbUi/components/modal/modal.vue'
import Button from 'bbUi/components/button/button.vue'

import PriceUploadDownloadTemplate from './PriceUploadDownloadTemplate.vue'
import PriceUploadImportData from './PriceUploadImportData.vue'
import PriceUploadInProgress from './PriceUploadInProgress.vue'

export default defineComponent({
  name: 'PriceUpload',
  components: {
    Modal,
    Button,
    PriceUploadDownloadTemplate,
    PriceUploadImportData,
    PriceUploadInProgress
  },
  props: {
    disabled: {
      default: false,
      type: Boolean
    },
    pro: {
      type: Boolean,
      default: true
    }
  },
  emits: ['submit'],
  setup(props, ctx) {
    const {
      modal,
      loading,
      loadingDownload,
      state,
      _state: storeState,
      modalCancelDownload,
      handleModalShow,
      handleUploadCompleted,
      handleUploadError,
      handleUploading,
      handleRemovedFile,
      handleClose,
      handleCloseWarning,
      handleUpdatePricing
    } = usePriceUpload()
    const { open: openPaywall } = usePaywall()

    const handleSubmit = async () => {
      if (!props.pro) {
        return openPaywall({
          title: 'Update Pricing via Price Sheet',
          description: 'To make manual bulk pricing updates easier, you can edit your prices offline using our XLSX or CSV templatesâ€”all the data is already there. Just update the prices, upload the file, and boom, youâ€™re done! Enjoy quick and efficient price adjustments across multiple items.',
          image_url: '/images/paywall/paywall-price-upload.svg'
        })
      }

      let typeFile = undefined
      const files = state.value.latestUploadedFile.map(i => i.file)
      if (!!files?.length) {
        typeFile = files[0].name.split('.').pop()
        const formData = new FormData()

        files.forEach((el, i) => {
          formData.append(`files[]`, el)
        })

        formData.append('type', typeFile === 'xlsx' ? 'excel' : 'csv')
        
        await handleUpdatePricing(
          formData,
          ctx.emit
        )
      }
    }
    const handleDownload = (e) => {
      loadingDownload.value = e
      modalCancelDownload.value = false
    }
    const handleShowValidation = () => {
      if (props.disabled) return
      handleModalShow()
    }
    const handleConfirmClose = () => {
      modal.value = false
      loadingDownload.value = false
      modalCancelDownload.value = false
    }

    return {
      modal,
      state,
      storeState,
      loading,
      loadingDownload,
      modalCancelDownload,
      handleShowValidation,
      handleClose,
      handleUploadCompleted,
      handleUploadError,
      handleUploading,
      handleRemovedFile,
      handleUpdatePricing,
      handleSubmit,
      handleDownload,
      handleCloseWarning,
      handleConfirmClose
    }
  }
})
</script>

